import {
    AuthException,
    PermissionException,
    SystemException,
    UserException
} from '@/utils/request/exception';

import { MessageBox } from 'element-ui';
import router from '@/router';
import store from '@/store';

async function errorHandler(res) {
    if (!res.status) {
        throw new Error('');
    }
    switch (res.status) {
        case 401: {
            if (res.data.errcode === 'AUTH_REQUIRE_REALNAME_VERIFY') {
                throw new UserException(
                    '',
                    res.data.errcode
                );
            }
            if (!store.getters.getToken) {
                throw new AuthException('', res);
            }
            store.commit('clearToken');
            // , 
            if (
                router.currentRoute &&
                router.currentRoute.name &&
                router.currentRoute.name != 'home'
            ) {
                await MessageBox.alert('', '', {
                    type: 'error',
                    confirmButtonText: '',
                    showClose: false
                });
                router
                    .replace({
                        name: 'home'
                    })
                    .catch((e) => e);
            }
            throw new AuthException('', res);
        }
        case 400: {
            throw new UserException(
                res.data.message || '',
                res.data.errcode
            );
        }
        case 403: {
            throw new PermissionException(
                res.data.message || '',
                res.data.errcode
            );
        }
        case 413: {
            throw new UserException('', res.data.errcode);
        }
        case 502:
        case 404: {
            throw new SystemException(
                `[${res.status}]`,
                res.data.errcode
            );
        }
        default: {
            throw new SystemException(
                '',
                res.data.errcode
            );
        }
    }
}

export default (axios) => {
    axios.interceptors.response.use(
        async (res) => {
            if (res.status >= 200 && res.status < 300) {
                return res.data.data || {};
            } else {
                await errorHandler(res);
            }
        },
        async (error) => {
            if (!error.response) {
                throw new Error('');
            }
            await errorHandler(error.response);
        }
    );
};
