<!--  -->
<template>
  <div class="body" id="full-Screen">
    <Header @fullScreenEvent="fullScreenEvent" />
    <div class="top">
      <div class="left">
        <router-link to="/">
          <div class="back-icon"></div>
        </router-link>
        <router-link to="/">
          <div class="back"></div>
        </router-link>
      </div>
      <div class="select-type">
        <div class="select-value">
          <div>{{ value }}</div>
          <img src="@/assets/image/home/ic_back_h.svg" alt="" />
        </div>
        <el-select v-model="value" placeholder="" @change="change">
          <el-option v-for="item in sendList" :key="item.value" :label="item.label" :value="item.value">
            <span>{{ item.value }}</span>
          </el-option>
        </el-select>
      </div>
    </div>
    <!-- <div id="toolbar-container" class="toolbar"></div> -->
    <div class="content">
      <div class="title">
        <textarea maxlength="200" v-model="title" placeholder="" cols="30" rows="10"></textarea>
        <div class="tip">{{ title.length }}/200</div>
      </div>
      <div class="center">
        <ImgContent :imgList="imgList" />
        <div class="upload-img" @click="chooseImages"></div>
      </div>

      <div id="text-container" class="text"></div>
      <div class="data">
        <div class="scope">:</div>
        <div class="select">
          <el-select v-model="openRange" placeholder="">
            <el-option v-for="item in scopeList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <!-- <div class="data">
        <div class="cover">:</div>
        <div class="img"></div>
        <span></span>
      </div> -->
      <div class="data">
        <div class="topic">:</div>
        <div class="select">
          <el-select v-model="topicId" placeholder="" filterable>
            <el-option v-for="item in topicList" :key="item.topic_id" :label="item.title" :value="item.topic_id">
            </el-option>
          </el-select>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="Buttons">
        <Button class="button1" name="" />
        <Button name="" @click.native="issue()" />
      </div>
    </div>
  </div>
</template>

<script>
import Header from "@/components/header/Header.vue";
import Button from "@/components/button/Button.vue";
import ImgContent from "@/components/imgContent/ImgContent.vue";
import { fullScreenMixin } from "@/mixins/mixins";
import { CloudDisk } from "@/utils/CloudDisk";
import { createPosts, getalltopicList } from "@/api/zhuanlan/zhuanlan";
export default {
  components: { Header, Button, ImgContent },
  mixins: [fullScreenMixin],
  data() {
    return {
      selectType: false,
      sendTitle: "",
      isMask: false,
      sendList: [
        { id: 1, value: "", label: "", isSelect: true },
        { id: 2, value: "", label: "", isSelect: false },
        { id: 3, value: "", label: "", isSelect: false },
      ],
      scopeList: [
        { value: "all", label: "" },
        { value: "current", label: "" },
      ],
      imgList: [],
      topicList: [],
      query: {
        pageNum: 1,
        pageSize: 100,
      },
      value: "",
      openRange: "all",
      contents: [],
      title: "",
      topicId: null,
    };
  },
  computed: {},

  mounted() {
    this.getTopicList();
  },

  methods: {
    async issue() {
      const data = {
        topicId: this.topicId || 0,
        title: this.title,
        contents: this.contents,
        postsType: 0,
        openRange: this.openRange,
      };
      try {
        await createPosts(data);
        this.$message({
          message: "",
          type: "success",
        });
        this.title = "";
      } catch (error) {
        this.$message.error("");
      }
    },
    async getTopicList() {
      try {
        const res = await getalltopicList(this.query);
        this.topicList = this.topicList.concat(res.list);
      } catch (error) {
        this.$message.error("");
      }
    },
    sendType(id) {
      this.sendList.forEach((item) => {
        if (item.id === id) {
          item.isSelect = true;
          this.sendTitle = item.title;
        } else {
          item.isSelect = false;
        }
      });
    },
    // 
    async chooseImages() {
      try {
        const res = await CloudDisk.uploadLocalImage({ folderId: 0 });
        res.map((item) => {
          this.contents.push({
            type: "img",
            value: item.fileId,
          });
        });
      } catch (error) {
        uni.showToast({
          title: error.message || error,
          icon: "none",
          mask: true,
        });
      }
    },
    change(e) {
      switch (e) {
        case "":
          this.$router.push({ path: "/creations/video" });
          return;
        case "":
          this.$router.push({ path: "/creations/zhuan_lan" });
      }
    },
  },
};
</script>
<style lang='scss' scoped>

@import "./dynaic.scss";
